
import { api } from "@/utils/api.js";
import XButton from 'vux/src/components/x-button/index.vue'

import { mapGetters } from "vuex";
export default {
name: "login",
props: {},
components: {
XButton
},
data() {
return {
phone: "",
code: "",
new_password1: "",
new_password2: "",
countdown: 60
};
},
created() {
console.log(this.user);
},
computed: {
...mapGetters(["user"])
},
methods: {

getcode() {
let reg = /^1[0-9]{10,}$/; 
let _test = reg.test(this.phone);
if (_test) {
this.setcode();
} else {
this.$vux.toast.text("请输入正确的手机号码", "top");
}
},
async setcode() {
let that = this;
function settime() {
if (that.countdown == 0) {
that.countdown = 60;
that.flag = false;
} else {
that.countdown--;
setTimeout(function() {
settime();
}, 1000);
}
}

if (!this.flag) {
this.flag = true;
let timestamp = Date.parse(new Date()) / 1000;
let param = {
plat: 3,
tel: this.phone
};
let data = {
param: this.$encrypt(JSON.stringify(param))
};
const [err, res] = await api.changephone_sms(data);
if (err) {
this.$vux.toast.text(err.msg);
this.flag = false;
return;
}
this.$vux.toast.text(res.msg);
settime();
} else {
console.log("函数防抖");
}
},

async checkInfo() {
let that = this;
let reg = /^1[0-9]{10,}$/; 
let _test = reg.test(this.phone);
if (!_test) {
this.$vux.toast.text("请输入正确的手机号");
return;
}
if (!this.code) {
this.$vux.toast.text("请输入正确的验证码");
return;
}
let data = {
plat: 3,
account: this.user.account,
token: this.user.token,
code: this.code,
tel: this.phone
};
this.$vux.loading.show({
text: "Loading"
});
const [err, res] = await api.changephone(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
this.$vux.toast.text(res.msg);
setTimeout(() => {
that.$router.go(-1);
}, 500);
}
}
};



